<template>
    <div style="height: 100%;width: 100%;" id="guide-main">
        <swiper :options="swiperOption" @someSwiperEvent="callback" ref="mySwiper"
                style="height: 100%;width: 100%;">
            <!-- 第一个屏幕 -->
            <swiper-slide>
                <first></first>
            </swiper-slide>
            <!-- 第二个屏幕 -->
            <swiper-slide>
                <second></second>
            </swiper-slide>
            <!-- 第三个屏幕 -->
            <swiper-slide>
                <third></third>
            </swiper-slide>
            <swiper-slide>
                <app-footer></app-footer>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <back-top :scroll="pageState" @click="backTop"></back-top>
    </div>
</template>

<script>
  import first from '@/pages/guide/guide1'
  import second from '@/pages/guide/guide2'
  import third from '@/pages/guide/guide3'
  import appFooter from '@/components/app-footer'
  import backTop from '@/components/back-top'

  export default {
    name: 'guide',
    components: {
      first,
      second,
      third,
      appFooter,
      backTop
    },
    data() {
      let _this = this
      return {
        swiperOption: {
          // 自定义块大小
          slidesPerView: 'auto',
          // 允许键盘切换
          keyboard: {
            enabled: true,
            onlyInViewport: true
          },
          // 使用分页导航
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
            // 解决了footer和分页器关联的问题
            renderBullet: function (index, className) {
              if (index === 3) {
                return ''
              } else {
                return '<span class="' + className + '"></span>'
              }
            }
          },
          // 禁止鼠标拖动切换
          simulateTouch: false,
          // 开启鼠标滚轮切换
          mousewheel: true,
          // 垂直切换
          direction: 'vertical',
          // 切换事件
          on: {
            // 切换之前的回调方法
            slideChangeTransitionStart: function () {
              _this.$store.commit('setPageState', this.activeIndex)
            }
          }
        }
      }
    },
    mounted() {
      this.$store.commit('setBackground', false)
    },
    methods: {
      callback() {
      },
      backTop() {
        this.$refs.mySwiper.swiper.slideTo(0, 1000, false)
        this.$store.commit('setPageState', 0)
      }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      },
      pageState() {
        return this.$store.state.pageState
      }
    }
  }
</script>

<style lang="less">
    .swiper-slide:last-child {
        height: 300px;
    }

    .swiper-pagination {
        height: 100px;
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-around;
        position: absolute;
        left: 2rem;


        .swiper-pagination-bullet {
            width: 12px;
            height: 12px;
            opacity: 0.2;
        }

        .swiper-pagination-bullet-active {
            opacity: 1;
            background: #000;
        }
    }
</style>
